
































































































import { Howl } from "howler";
import { Component, Prop, Vue, Watch } from "vue-property-decorator";

@Component
export default class MusicCard extends Vue {
  @Prop() private track: any;

  // @Prop() private player: Howl;

  @Watch("track")
  trackDidChange() {
  }
  progress: number;
  animationQ: any;
  isPlaying: boolean;
  isFavorite: boolean;
  isSeeking: boolean;
  player: any;
  constructor() {
    super();
    this.progress = 0;
    this.isPlaying = false;
    this.isFavorite = false;
    this.isSeeking = false;
    this.player = null;


  }
  setProgress() {
    if (!this.isSeeking) {
      let seek: any = this.player.seek();
      this.progress = seek / this.player.duration() * 100 || 0;
      requestAnimationFrame(this.setProgress.bind(this));
    }
  }
  playerEnded() {
    this.isPlaying = false;
  }

  fullImage() {
    if (!!this.track) {
      return this.track.artworkUrl100.replace(/100x100bb/, "400x400bb");
    }
  }

  toggleSong() {
    if (this.isPlaying) {
      this.stopSong();
    } else {
      this.playSong();
    }
  }
  playSong() {
    this.player.play();
    this.isPlaying = true;
  }
  stopSong() {
    this.isPlaying = false;
    this.player.stop();
    window.cancelAnimationFrame(this.animationQ);
  }
  seek(val: any) {
    this.isSeeking = false;
    let duration = this.player.duration();
    this.player.seek(duration * (val / 100));
    requestAnimationFrame(this.setProgress.bind(this));
  }
  handleDrag() {
    this.isSeeking = true;
  }
  mounted(){
    this.player = new Howl({
      src: [this.track.previewUrl],
      onplay: this.setProgress.bind(this),
      onend: this.playerEnded.bind(this)
    });
  }
}



