















































































import { Component, Vue, Prop, Watch, Inject } from "vue-property-decorator";
import { debounceTime } from "rxjs/operators/debounceTime";
import { tap } from "rxjs/operators/tap";
import { switchMap } from "rxjs/operators/switchMap";
import { filter } from "rxjs/operators/filter";
import { load } from "@/providers/itunes";
import { Subject } from "rxjs/Subject";
import MusicCard from "@/components/MusicCard.vue";
@Component({
    components: {
      MusicCard
    }
})
export default class SearchPage extends Vue {
  private searchInput: string = "";
  private onInput$: any;
  private isError: boolean = false;
  public showSpinner: boolean = false;
  public track = null;
  public showCard = false;
  public listing = [];

  private setSearch(trending: string) {
    this.searchInput = trending;
    this.onInput$.next(trending);
  }
  private searchCleared(event: any) {
    this.searchInput = "";
    this.listing = [];
  }
  private goToDetail(track: any) {
    this.showCard = true
    this.track = track
    // this.$router.push({ name: 'trackDetail', params: { id: track.trackId }})
  }
  protected mounted() {
    this.onInput$ = new Subject<string>();
    this.onInput$
      .pipe(
        filter((term: any) => {
          if (term) {

            this.showSpinner = true;
            this.isError = false;
            console.log(this.showSpinner)
            return term;
          } else {
            this.listing = [];
            this.showSpinner = false;
          }
        }),
        debounceTime(500),
        switchMap((term: string) => load(term)),
        tap(() => {
          this.showSpinner = false;
        })
      )
      .subscribe(
        ( results: any ) => (this.listing = results.results),
        ( err: any ) => {
          this.showSpinner = false;
          this.isError = true;
          console.log(err);
        }
      );
  }
  clear() {
    this.track = null;
    this.showCard = false;
  }
}



